
import http from 'js/http'
import Loading from 'vux/src/components/loading/index.vue'

import {mapState, mapMutations, mapGetters} from 'vuex'

export default {
name: "index",
components: {
Loading,
},
data() {
return {
currencyList: [],
listShow: true
}
},
methods: {
openCollect() {
this.$router.push({name: 'collect', params: {address: this.walletData.address}})
},
getCurrencyList() {
http.post('/wallet/currency', {}, (res) => {
this.listShow = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.currencyList = res.content

sessionStorage.setItem("currencyList", JSON.stringify(this.currencyList));
})
},
pageInt() {
if (!this.loginMsg) {
this.$router.push({
path: `/login`
})
return
}
this.currencyList = []
this.listShow = true
this.getCurrencyList()
/*if (!this.currencyList.length){
this.getCurrencyList()
}*/

}
},
created() {

},
activated() {
this.pageInt()
},
computed: {
...mapGetters([
"loginMsg",
]),
},
watch: {
'$route': function (t, f) {
if (t.path === '/assets') {
this.pageInt()
}
}
}
}



